.wbs-doc-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: linear-gradient(135deg, #f5f7fa 0%, #f0f4f8 100%);
  padding: 0;
  overflow-y: auto;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
}

.wbs-header {
  background: linear-gradient(135deg, #ff6b35 0%, #f7931e 100%);
  color: white;
  padding: 16px 30px;
  text-align: center;
  box-shadow: 0 4px 20px rgba(255, 107, 53, 0.3);
  
  h1 {
    margin: 0;
    font-size: 24px;
    font-weight: 700;
    letter-spacing: -0.5px;

    .folder-tag {
      margin-left: 8px;
      font-size: 14px;
      font-weight: 500;
      opacity: 0.8;
    }
  }
  
  .subtitle {
    margin: 3px 0 0 0;
    font-size: 12px;
    opacity: 0.9;
  }
}

.wbs-content {
  flex: 1;
  padding: 30px;
  overflow-y: auto;
  
  &::-webkit-scrollbar {
    width: 8px;
  }
  
  &::-webkit-scrollbar-track {
    background: transparent;
  }
  
  &::-webkit-scrollbar-thumb {
    background: rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    
    &:hover {
      background: rgba(0, 0, 0, 0.3);
    }
  }
}

.wbs-legend {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
  background: white;
  padding: 15px 20px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  flex-wrap: wrap;
}

.legend-item {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 13px;
  color: #555;
}

.legend-color {
  width: 16px;
  height: 16px;
  border-radius: 4px;
}

.wbs-table {
  background: white;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  border: 1px solid #e0e0e0;
  margin-bottom: 20px;
}

.table-header {
  display: grid;
  grid-template-columns: 2fr 1.5fr 1.5fr 1.5fr;
  background: #f8f9fa;
  border-bottom: 1px solid #e0e0e0;
  font-weight: 600;
  font-size: 12px;
  color: #333;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  
  div {
    padding: 15px 12px;
    text-align: left;
  }
}

.table-row {
  display: grid;
  grid-template-columns: 2fr 1.5fr 1.5fr 1.5fr;
  border-bottom: 1px solid #e0e0e0;
  font-size: 13px;
  align-items: center;
  
  &:last-child {
    border-bottom: none;
  }
  
  &:hover {
    background: #fafbfc;
  }
  
  div {
    padding: 12px;
  }
}

.col-name {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #333;
}

.expand-btn {
  width: 20px;
  height: 20px;
  border: none;
  background: none;
  cursor: pointer;
  color: #ff6b35;
  font-size: 12px;
  font-weight: bold;
  transition: transform 0.2s;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  
  &.expanded {
    transform: rotate(90deg);
  }
  
  &:hover {
    color: #f7931e;
  }
}

.no-expand {
  width: 20px;
  display: inline-block;
}

.item-name {
  font-weight: 500;
}

.col-dates {
  color: #666;
  font-size: 12px;
}

.date-range {
  white-space: nowrap;
}

.col-progress {
  display: flex;
  align-items: center;
  gap: 8px;
}

.progress-bar {
  flex: 1;
  height: 6px;
  background: #e0e0e0;
  border-radius: 3px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  border-radius: 3px;
  transition: width 0.3s;
}

.progress-text {
  font-size: 12px;
  font-weight: 600;
  color: #555;
  min-width: 35px;
  text-align: right;
}

.col-responsible {
  text-align: center;
}

.responsible-badge {
  display: inline-block;
  background: #f3f4f6;
  color: #555;
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 500;
  white-space: nowrap;
}

.wbs-summary {
  background: white;
  padding: 25px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  
  h2 {
    margin: 0 0 20px 0;
    font-size: 16px;
    font-weight: 600;
    color: #333;
    padding-bottom: 10px;
    border-bottom: 2px solid #ff6b35;
  }
}

.summary-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 15px;
}

.summary-item {
  background: #f9fafb;
  padding: 15px;
  border-radius: 6px;
  border-left: 3px solid #ff6b35;
}

.summary-label {
  font-size: 12px;
  color: #777;
  font-weight: 500;
  margin-bottom: 5px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.summary-value {
  font-size: 20px;
  font-weight: 700;
  color: #ff6b35;
}
